import {
  Box,
  Flex,
  HStack,
  Link,
  IconButton,
  Button,
  useDisclosure,
  Stack,
  Heading,
} from "@chakra-ui/react";
import { HamburgerIcon, CloseIcon } from "@chakra-ui/icons";

const Links = ["Home", "About", "Skills", "Project", "Contact"];

const NavLink = ({ children }) => (
  <Link
    px={2}
    color={"#fff"}
    py={1}
    rounded={"md"}
    _hover={{
      textDecoration: "none",
      color: "#4299E1",
    }}
    href={"#skills"}>
    {children}
  </Link>
);

export default function Simple() {
  const { isOpen, onOpen, onClose } = useDisclosure();

  return (
    <>
      <Box
        // color={"#FFB74D"}
        bg={"#1f1f1f"}
        px={4}
        position={"fixed"}
        w={"100%"}
        zIndex={1}
        top={0}>
        <Flex
          h={16}
          alignItems={"center"}
          justifyContent={"space-between"}
          flexDirection={"row-reverse"}>
          <IconButton
            size={"md"}
            icon={isOpen ? <CloseIcon /> : <HamburgerIcon />}
            aria-label={"Open Menu"}
            display={{ md: "none" }}
            onClick={isOpen ? onClose : onOpen}
          />
          <HStack spacing={8} alignItems={"center"}>
            <HStack
              as={"nav"}
              spacing={4}
              display={{ base: "none", md: "flex" }}>
              {Links.map((link) => (
                <NavLink key={link}>{link}</NavLink>
              ))}
              <Button
                bg={"#1f1f1f"}
                _hover={{ backgroundColor: "#1f1f1f", color: "#4299E1" }}>
                Resume
              </Button>
            </HStack>
          </HStack>
          <Flex alignItems={"center"}>
            <Heading
              ml={10}
              fontSize={"2xl"}
              display={"flex"}
              alignItems={"center"}>
              <Box fontSize={"35px"} fontFamily={"arial"}>
                S
              </Box>
              uraj
            </Heading>
          </Flex>
        </Flex>

        {isOpen ? (
          <Box pb={4} display={{ md: "none" }}>
            <Stack as={"nav"} spacing={4}>
              {Links.map((link) => (
                <NavLink key={link}>{link}</NavLink>
              ))}
            </Stack>
          </Box>
        ) : null}
      </Box>

      <Box p={4}>content</Box>
    </>
  );
}
