import {
  Box,
  Button,
  Flex,
  Heading,
  // Image,
  Stack,
  Text,
} from "@chakra-ui/react";
// import Logo from "../Images/Suraj-yadav-image.png";
export default function SplitScreen() {
  return (
    <Stack minH={"100vh"} direction={{ base: "column", md: "row" }} id="home">
      <Flex p={8} flex={1} align={"center"} justify={"center"}>
        <Stack spacing={4} w={"full"} maxW={"xl"}>
          <Heading fontSize={{ base: "3xl", md: "4xl", lg: "5xl" }}>
            <Text
              as={"span"}
              position={"relative"}
              //   _after={{
              //     content: "''",
              //     width: "full",
              //     height: useBreakpointValue({ base: "20%", md: "30%" }),
              //     position: "absolute",
              //     bottom: 1,
              //     left: 0,
              //     bg: "blue.400",
              //     zIndex: -1,
              //   }}
            >
              <Box display={"flex"} gap={2}>
                <Text> Hi all, </Text> <Text color={"#fff"}>I'm Suraj</Text>
              </Box>
            </Text>
            <Text color={"blue.400"} as={"span"} fontSize={"4xl"}>
              Full Stack Web Developer
            </Text>{" "}
          </Heading>
          <Text fontSize={{ base: "md", lg: "lg" }} color={"gray.500"}>
            Self-driven, quick starter, passionate programmer with a curious
            mind who enjoys solving problems and working on new projects.
          </Text>
          <Stack direction={{ base: "column", md: "row" }} spacing={4}>
            <Button
              rounded={"full"}
              bg={"blue.400"}
              color={"white"}
              _hover={{
                bg: "blue.500",
              }}>
              Resume
            </Button>
          </Stack>
        </Stack>
      </Flex>
      <Flex flex={1} justifyContent={"center"} alignItems={"center"}>
        {/* <Image
          alt={"Login Image"}
          objectFit={"cover"}
          h={["250px", "350px", "500px"]}
          src={Logo}
          borderRadius={"5%"}
        /> */}
      </Flex>
    </Stack>
  );
}
